            @include box-shadow($defaultShadow2...);
            padding: 1rem;

            .util-wrap {
              color: $gray9;
              margin-left: 10px;
            }
        }
        .btn-del {
          position: relative;
           @include icon-x();
        }
        .item-date {
          position: absolute;
          bottom: 0.5rem;
          right: 1rem;
          font-size: 0.5rem;
        }
    }
</style>